import Swiper, { Pagination, Manipulation } from 'swiper';
import 'swiper/scss';
import 'swiper/scss/pagination';

// swiper
const initSize = 513;
const swipePrice = document.querySelector('.prices__swiper');
let swiperPrice;

window.addEventListener('resize', function () {
  mobileSlider();
});

if (window.innerWidth > initSize) {
  addPriceTable();
} else {
  mobileSlider();
}

function mobileSlider() {
  if (window.innerWidth <= initSize && swipePrice.dataset.mobile == 'false') {
    swiperPrice = new Swiper(swipePrice, {
      slidesPerView: 1,
      spaceBetween: 16,
      modules: [Pagination, Manipulation],
      slideClass: 'prices__slide',
      wrapperClass: 'prices__wrapper',
      pagination: {
        el: '.prices-pagination',
      },
    });
    swipePrice.dataset.mobile = 'true';

    if (swipePrice.classList.contains('swiper-initialized')) {
      addPriceSlide();
      removePriceTable();
    }
  }

  if (window.innerWidth > initSize) {
    swipePrice.dataset.mobile = 'false';
    if (swipePrice.classList.contains('swiper-initialized')) {
      swiperPrice.removeSlide([0, 1, 2, 3, 4]);
      swiperPrice.destroy();
      addPriceTable();
    }
  }
}

function addPriceSlide() {
  swiperPrice.appendSlide([
    `<div class="prices__slide">
    <div class="prices__element"><span> </span><p>    </p></div>
    <div class="prices__element"><span></span><p></p></div>
    <div class="prices__element"><span></span><p>30-120 </p><button type="button"><img src="img/icon-order.svg" alt=" "></button></div>
    </div>`,
    `<div class="prices__slide">
    <div class="prices__element"><span> </span><p></p></div>
    <div class="prices__element"><span></span><p></p></div>
    <div class="prices__element"><span></span><p>30 </p><button type="button"><img src="img/icon-order.svg" alt=" "></button></div>
    </div>`,
    `<div class="prices__slide">
    <div class="prices__element"><span> </span><p> </p></div>
    <div class="prices__element"><span></span><p>1 000 </p></div>
    <div class="prices__element"><span></span><p>30-120 </p><button type="button"><img src="img/icon-order.svg" alt=" "></button></div>
    </div>`,
    `<div class="prices__slide">
    <div class="prices__element"><span> </span><p>  </p></div>
    <div class="prices__element"><span></span><p>1 000 </p></div>
    <div class="prices__element"><span></span><p>30-120 </p><button type="button"><img src="img/icon-order.svg" alt=" "></button></div>
    </div>`,
    `<div class="prices__slide">
    <div class="prices__element"><span> </span><p>  </p></div>
    <div class="prices__element"><span></span><p>1 000 </p></div>
    <div class="prices__element"><span></span><p>30-120 </p><button type="button"><img src="img/icon-order.svg" alt=" "></button></div>
    </div>`,
  ]);
}

function addPriceTable() {
  swipePrice.insertAdjacentHTML(
    'afterbegin',
    `<table class="prices__table">                    
    <tr>
        <th> </th>
        <th></th>
        <th></th>
        <th></th>
    </tr>
    <tr>
        <td></td>
        <td></td>
        <td>30 </td>
        <td><button type="button"><img src="img/icon-order.svg" alt=" "></button></td>
    </tr>
    <tr>
        <td> </td>
        <td>1 000 </td>
        <td>30-120 </td>
        <td><button type="button"><img src="img/icon-order.svg" alt=" "></button></td>
    </tr>
    <tr>
        <td>  </td>
        <td>1 000 </td>
        <td>30-120 </td>
        <td><button type="button"><img src="img/icon-order.svg" alt=" "></button></td>
    </tr>
    <tr>
        <td>    </td>
        <td>1 000 </td>
        <td>30-120 </td>
        <td><button type="button"><img src="img/icon-order.svg" alt=" "></button></td>
    </tr>
    <tr>
        <td>  </td>
        <td>1 000 </td>
        <td>30-120 </td>
        <td><button type="button"><img src="img/icon-order.svg" alt=" "></button></td>
    </tr>
    </table>`
  );
}

function removePriceTable() {
  if (document.querySelector('.prices__table')) {
    document.querySelector('.prices__table').remove();
  }
}
